body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  background-color: #1f0a4e;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

img {
  width: 100%;
  height: auto;
}

a {
  text-decoration: none;
  color: #FFFFFF;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

/* Estilo para eliminar el borde al hacer clic en un input */
input:focus {
  outline: none;
}

.txt-negritas {
  font-weight: 700;
}

.txt-italica {
  font-style: italic;
}

.txt-uppercase {
  text-transform: uppercase;
}

.txt-center {
  text-align: center;
}

.posicionador {
  z-index: -1;
  position: absolute;
  width: 100%;
  height: 100%;
}

.txt-verde {
  color: #329f00;
}

.esconder {
  display: none;
}

.bg-claro {
  width: 100%;
  background-color: #4618ac;
  padding-top: 3rem;
  padding-bottom: 3rem;
}

.bg-oscuro {
  width: 100%;
  background-color: #1f0a4e;
  padding-top: 3rem;
  padding-bottom: 3rem;
}

.seleccionado {
  position: relative;
  user-select: none;
}
.seleccionado input {
  pointer-events: none;
  user-select: none;
}
.seleccionado input[type=checkbox] {
  opacity: 0;
}
.seleccionado label {
  display: block;
}

.seleccionado::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(255, 255, 255, 0.4); /* Gris semi-transparente */
  pointer-events: none; /* Evita que la capa bloquea los eventos del ratón */
  z-index: 1; /* Coloca la capa encima del contenido */
}

.w-100 {
  width: max-content;
}

