  color: #FFFFFF;
  font-weight: 800;
}
@media only screen and (min-width: 768px) {
  .login_bienvenida {
    margin-bottom: 0;
    font-size: 5rem;
  }
}

.formulario {
  padding: 4rem 1rem;
  background-color: #4618ac;
}
.formulario fieldset {
  border: none;
}
.formulario legend {
  font-size: 2rem;
