}

.carrusel-flecha-izquierda,
.carrusel-flecha-derecha {
  font-size: 3rem;
  padding: 0.5rem;
  cursor: pointer;
  position: absolute;
  z-index: 2;
  top: 40%;
  color: #FFFFFF;
  transition: 0.2s ease-in-out all;
}
.carrusel-flecha-izquierda:hover,
.carrusel-flecha-derecha:hover {
  transform: scale(1.3);
}

.carrusel-flecha-izquierda {
